<template>
  <div class="comment">
    <h3>{{comment.total}}条评论</h3>
    <div class="comMsg" v-for="(item, index) in comment.list" :key="index">
      <div class="left">
        <img :src="item.userinfo.avatar" alt="">
      </div>
      <div class="right">
        <div class="commentTit">
          <div class="lt">
            <h4>{{item.userinfo.username}}</h4>
            <span>{{timeago(item.addtime)}}</span>
          </div>
          <div class="rt">
            <van-icon name="good-job-o" />
            <span>{{item.count_approve}}</span>
          </div>
        </div>
        <div class="commentCon">
          {{item.content}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "VideoComment",
    props: ['comment'],
    methods: {
      timeago(dateTimeStamp) {
        var minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
        var hour = minute * 60;
        var day = hour * 24;
        var week = day * 7;
        var month = day * 30;
        var now = new Date().getTime(); //获取当前时间毫秒
        var diffValue = now - dateTimeStamp * 1000; //时间差
        if (diffValue < 0) {
          return '传入的时间戳大于当前时间戳';
        }
        var minC = diffValue / minute; //计算时间差的分，时，天，周，月
        var hourC = diffValue / hour;
        var dayC = diffValue / day;
        var weekC = diffValue / week;
        var monthC = diffValue / month;
        var result;
        if (monthC >= 1 && monthC <= 3) {
          result = " " + parseInt(monthC) + "月前"
        } else if (weekC >= 1 && weekC <= 3) {
          result = " " + parseInt(weekC) + "周前"
        } else if (dayC >= 1 && dayC <= 6) {
          result = " " + parseInt(dayC) + "天前"
        } else if (hourC >= 1 && hourC <= 23) {
          result = " " + parseInt(hourC) + "小时前"
        } else if (minC >= 1 && minC <= 59) {
          result = " " + parseInt(minC) + "分钟前"
        } else if (diffValue >= 0 && diffValue <= minute) {
          result = "刚刚";
        }else{
          result = '远古时期'
        }
        return result;
      }
    },
  }
</script>

<style lang="less">
  .comment {
    border-top: 1px solid rgba(188, 188, 188, 0.3);
    border-bottom: 1px solid rgba(188, 188, 188, 0.3);
    padding: 30px 10px;
    box-sizing: border-box;

    h3 {
      font-size: 16px;
      font-weight: bold;
      margin: 5px 0;
    }

    .comMsg {
      padding: 15px 0;
      display: flex;
      box-sizing: border-box;
      border-bottom: 1px solid rgba(188, 188, 188, 0.3);

      .left {
        height: 40px;
        width: 40px;
        margin-right: 10px;

        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }

      .right {
        flex: 1;
        padding: 0 10px;
        box-sizing: border-box;

        .commentTit {
          display: flex;
          margin-bottom: 10px;
          .lt {
            flex: 1;
            h4{
              font-weight: bold;
            }
            span{
              font-size: 12px;
              color: #999;
            }
          }

          .rt {}
        }

      }
    }
  }
</style>