import "../plugins/axios";

// 热词
export const getSearch=function(){
  return window.axios.get('apiv3/search/index').catch(error=>{
    console.log(error);
  })
}

export const searchKeyWord=function (keyWord) { 
  return window.axios.get('apiv3/search',{
    params:{
      kw:keyWord
    }
  }).catch(error=>{
    console.log(error);
  })
 }