<template>
  <div class="search">
    <div class="head">
      <div class="input">
        <van-icon name="search" />
        <form action="javascript:void(0)" @submit="getSearchData">
          <input type="text" placeholder="请输入要搜索的内容" v-model="searchText">
        </form>
        <van-icon name="close" v-show="searchText!=''" @click="defaultShow()" />
      </div>
      <span @click="$emit('searchShow',false)">取消</span>
    </div>
    <template v-if="!submitData">
      <div class="hotSearch">
        <h3>热门搜索</h3>
        <div class="hotBox">
          <span v-for="(item, index) in hotSearch" :key="index" @click="searchItem(item.kw)">{{item.kw}}</span>
        </div>
      </div>
      <div class="history">
        <h3>搜索历史</h3>
        <template v-if="hasHistory">
          <div class="hotBox">
            <span v-for="(item, index) in historyData" :key="index" @click="searchItem(item)">{{item}}</span>
          </div>
          <div class="none" @click="clearAll()">
            <span>清除所有搜索记录</span>
          </div>
        </template>
        <template v-else>
          <div class="none">
            <span>还没有任何搜索记录</span>
          </div>
        </template>
      </div>
    </template>
    <template v-else>
      <van-dropdown-menu>
        <van-dropdown-item v-model="value1" :options="option1" />
        <van-dropdown-item v-model="value2" :options="option2" />
        <van-dropdown-item title="全部频道" ref="item">
          <div class="hotBox">
            <span v-for="(item, index) in searchFilterCate" :key="index"
              @click="onConfirm(item.catename)">{{item.catename}}</span>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
      <div class="showSearchData">
      <ViewPart v-for="(item, index) in searchKeywords" :key="index" :item="item"></ViewPart>
      </div>
    </template>
  </div>

</template>

<script>
  import types from "@/store/TYPES.js";
  import ViewPart from "@/components/ViewPart.vue";
  import {
    mapState
  } from "vuex";
  export default {
    name: "Search",
    components:{
      ViewPart
    },
    data() {
      return {
        searchText: "",
        submitData: false,
        historyData: [],
        value1: null,
        value2: null,
      }
    },
    computed: {
      ...mapState({
        hotSearch: state => state.hotSearchData.recommend_keywords,
        searchFilterType: state => state.hotSearchData.filter.type,
        searchFilterCate: state => state.hotSearchData.filter.cate,
        searchOrder: state => state.hotSearchData.order,
        searchKeywords:state=>state.searchKeywords,
      }),
      hasHistory: function () {
        return this.historyData.length > 0 ? true : false
      },
      option1: function () {
        return this.searchFilterType.map(item => {
          return {
            text: item.key,
            value: item.value
          }
        })
      },
      option2: function () {
        return this.searchOrder.map(item => {
          return {
            text: item.key,
            value: item.value
          }
        })
      },

    },
    methods: {
      getSearchData() {
        if (this.searchText.toString().trim() != "") {
          this.submitData = true;
          this.$store.dispatch(types.SEARCH_KEY_WORD, this.searchText.toString().trim());
          this.historyData.push(this.searchText.toString().trim());
          // 去除重复
          this.historyData = this.historyData.filter(function (ele, index, self) {
            return self.indexOf(ele) === index;
          });
          window.localStorage.setItem('historyData', JSON.stringify(this.historyData))
        }
      },
      defaultShow() {
        this.searchText = '';
        this.submitData = false;
      },
      searchItem(v) {
        this.searchText = v;
        this.getSearchData();
      },
      clearAll() {
        window.localStorage.removeItem('historyData');
        this.historyData = [];
      },
      onConfirm(v) {
        this.$refs.item.toggle();
        this.searchItem(v)
      },

    },
    created() {
      this.$store.dispatch(types.HOT_SEARCH_GET);
      this.value1 = this.searchFilterType[0].value;
      this.value2 = this.searchOrder[0].value;
      let hasHistoryData = JSON.parse(window.localStorage.getItem('historyData'));
      if (hasHistoryData) this.historyData = hasHistoryData;
    }
  }
</script>

<style lang="less">
  .search {
    position: fixed;
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    z-index: 4;
    background-color: black;
    box-sizing: border-box;
    overflow-y: scroll;
   

    .head {
      display: flex;
      align-items: center;
      height: 60px;
      width: 100%;
      position: fixed;
      background-color: black;
      left:0;
      top:0;
      z-index: 5;
      .input {
        flex: 1;
        display: flex;
        align-items: center;
        background-color: #222;
        padding: 0.5em 0;
        border-radius: 5px;

        .van-icon {
          margin: 0 10px;
        }

        form {
          flex: 1;

          input {
            border: none;
            outline: none;
            background: none;
            width: 100%;

            &::-webkit-input-placeholder {
              color: #999;
            }
          }

        }
      }

      span {
        font-size: 16px;
        padding: 10px;
        color: #999;
      }
    }

    .hotBox {
      padding: 20px 10px;
      box-sizing: border-box;
      display: flex;
      flex-wrap: wrap;

      span {
        font-size: 14px;
        padding: 0.5em;
        margin: 0 15px 15px 0;
        background-color: #222;
        line-height: 1em;
        color: #ccc;
      }
    }

    .hotSearch {
      margin: 80px 0 10px;
    }

    .history {
      .none {
        margin-top: 10px;
        text-align: center;
        font-size: 12px;
        color: #999;
      }
    }
    .hotSearch, .history{
       h3 {
      color: #999;
      padding: 5px 10px;
    }
    }
    .van-dropdown-menu {
       position: fixed;
      left:0;
      top:60px;
      z-index: 5;
      width: 100%;
      .van-dropdown-menu__bar {
        background-color: black;

        .van-ellipsis {
          color: #999;
        }
      }

      .van-popup {
        background-color: black;
      }

      .van-cell {
        background-color: black;
        color: #999;
      }

      .van-dropdown-item__option--active {
        color: white;
      }
    }
    .showSearchData{
      margin-top: 100px;
    }
  }
</style>