<template>
  <div class="channel">
    <channel-show v-for="(item, index) in showData" :key="index" :theme="item" :num="index"
      @detailShow="detailShow=$event" @channelDetailData="channelDetailData=$event">{{item.catename}}</channel-show>
    <transition name="custom-classes-transition" enter-active-class="animated slideInRight"
      leave-active-class="animated slideOutRight">
      <ChannelDetail v-if="detailShow" @detailShow="detailShow=$event" :detailData="detailData"
      :channelDetailData="channelDetailData" >
      </ChannelDetail>
    </transition>
  </div>
</template>

<script>
  import {
    mapState
  } from "vuex";
  import types from "@/store/TYPES.js";
  import ChannelShow from "@/components/ChannelShow.vue";
  import ChannelDetail from "@/components/ChannelDetail.vue";
  export default {
    name: "Channel",
    components: {
      ChannelShow,
      ChannelDetail,
    },
    data() {
      return {
        detailShow: false,
        channelDetailData: {},
      }
    },
    computed: {
      ...mapState({
        showData: state => state.channelData,
        detailData: state => state.tabData
      }),
    },
    created() {
      this.$store.dispatch(types.CHANNEL_INDEX_DATA, {
        n: 10
      });

    },
  };
</script>

<style lang="less" scoped>
  .channel {
    display: flex;
    flex-wrap: wrap;
  }
</style>