import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Channel from "../views/Channel.vue";
import Person from "../views/Person.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/channel",
    name: "Channel",
    component: Channel
  },
  {
    path: "/person",
    name: "Person",
    component: Person
  }
];
const router = new VueRouter({
  routes,
  mode: 'history'
});
export default router;
