import Vue from "vue";
import Vuex from "vuex";
import types from "./TYPES";

// calendar
import {
  getCalendar
} from "../api/home";

// home
import {
  getIndexData
} from "../api/home";

// channel
import {
  getChannelData
} from "../api/channel";
import {
  getTabData
} from "../api/channel";
import {
  getCateData
} from "../api/channel";

// detailData
import {
  detailData
} from "../api/home";

// search
import {
  getSearch
} from "../api/search";

import {
  searchKeyWord
} from "../api/search";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    indexData: {},
    historyData: [],
    calendarData: [],
    channelData: [],
    tabData: [],
    detailData: [],
    hotSearchData: {},
    searchKeywords: [],
    loading: false
  },
  mutations: {
    // 日历数据
    [types.CALENDAR_DATA](state, playload) {
      state.calendarData = playload.result;
    },
    // 首页初始数据
    [types.INIT_INDEX_DATA](state, playload) {
      state.indexData = playload.result;
    },
    // 历史数据
    [types.GET_MORE_HISTORY_DATA](state, playload) {
      state.historyData.push(playload.result);
    },
    // 频道数据显示
    [types.CHANNEL_INDEX_DATA](state, playload) {
      state.channelData = playload.result;
    },
    // 频道详情数据显示
    [types.CHANNEL_DETAIL_DATA](state, playload) {
      state.tabData = playload.result;
    },
    // 播放页面详细数据
    [types.VIDEO_DETAIL_DATA](state, playLoad) {
      state.detailData = playLoad.result;
    },
    // 热词搜索
    [types.HOT_SEARCH_GET](state, playLoad) {
      state.hotSearchData = playLoad.result;
    },
    // 关键词搜索
    [types.SEARCH_KEY_WORD](state, playLoad) {
      state.searchKeywords = playLoad.result;
    },
    showLoading(state) {
      state.loading = true
    },
    hideLoading(state) {
      state.loading = false
    }
  },
  // 异步获取数据
  actions: {
    [types.CALENDAR_DATA](content) {
      var hasCalendarData = JSON.parse(window.localStorage.getItem('calendarData'));

      if (hasCalendarData && hasCalendarData.expires > Date.now()) {
        content.commit(types.CALENDAR_DATA, {
          result: hasCalendarData.result
        });
      } else {
        getCalendar().then(response => {
          // console.log(response.data.data);
          window.localStorage.setItem('calendarData', JSON.stringify({
            expires: Date.now() + 1 * 60 * 60 * 1000,
            result: response.data.data
          }));
          content.commit(types.CALENDAR_DATA, {
            result: response.data.data
          });
        });
      }
    },

    [types.INIT_INDEX_DATA](content) {
      var hasIndexData = JSON.parse(window.localStorage.getItem('IndexData'));
      if (hasIndexData && hasIndexData.expires > Date.now()) {
        content.commit(types.INIT_INDEX_DATA, {
          result: hasIndexData.result
        });
      } else {
        getIndexData().then(response => {
          // console.log(response.data.data);
          window.localStorage.setItem('IndexData', JSON.stringify({
            expires: Date.now() + 10 * 60 * 1000,
            result: response.data.data
          }));
          content.commit(types.INIT_INDEX_DATA, {
            result: response.data.data
          });
        });
      }
    },

    [types.CHANNEL_INDEX_DATA](content) {
      var hasChannelData = JSON.parse(window.localStorage.getItem('channelData'));
      if (hasChannelData && hasChannelData.expires > Date.now()) {
        content.commit(types.CHANNEL_INDEX_DATA, {
          result: hasChannelData.result
        });
      } else {
        getChannelData().then(response => {
          // console.log(response.data.data);
          window.localStorage.setItem('channelData', JSON.stringify({
            expires: Date.now() + 1 * 60 * 60 * 1000,
            result: response.data.data
          }));
          content.commit(types.CHANNEL_INDEX_DATA, {
            result: response.data.data
          });
        });
      }
    },

    [types.CHANNEL_DETAIL_DATA](content, tab) {
      // console.log(content.state.channelData[tab.num].cate_type=='0');
      var hasTabData = JSON.parse(window.localStorage.getItem('tabData' + tab.num));
      if (hasTabData && hasTabData.expires > Date.now()) {
        content.commit(types.CHANNEL_DETAIL_DATA, {
          result: hasTabData.result
        });
      } else {
        if (content.state.channelData[tab.num].cate_type != '0') {
          getTabData(tab.p, tab.tab).then(response => {
            window.localStorage.setItem('tabData' + tab.num, JSON.stringify({
              expires: Date.now() + 1 * 60 * 60 * 1000,
              result: response.data.data
            }));
            content.commit(types.CHANNEL_DETAIL_DATA, {
              result: response.data.data
            });
          });
        } else {
          getCateData(tab.p, tab.id).then(response => {
            // console.log(response,tab.id);
            window.localStorage.setItem('tabData' + tab.num, JSON.stringify({
              expires: Date.now() + 1 * 60 * 60 * 1000,
              result: response.data.data
            }));
            content.commit(types.CHANNEL_DETAIL_DATA, {
              result: response.data.data
            });
          });
        }
      }
    },
    [types.VIDEO_DETAIL_DATA](content, id) {
      var hasVideoData = JSON.parse(window.localStorage.getItem('videoData' + id));
      if (hasVideoData && hasVideoData.expires > Date.now()) {
        content.commit(types.VIDEO_DETAIL_DATA, {
          result: hasVideoData.result
        });
      } else {
        detailData(id).then(response => {
          window.localStorage.setItem('videoData' + id, JSON.stringify({
            expires: Date.now() + 24 * 60 * 60 * 1000,
            result: response.data.data
          }));
          content.commit(types.VIDEO_DETAIL_DATA, {
            result: response.data.data
          });
        });
      }
    },
    [types.HOT_SEARCH_GET](content) {
      var hasHotSearch = JSON.parse(window.localStorage.getItem('hotSearch'));
      if (hasHotSearch && hasHotSearch.expires > Date.now()) {
        content.commit(types.HOT_SEARCH_GET, {
          result: hasHotSearch.result
        });
      } else {
        getSearch().then(response => {
          // console.log(response.data.data.recommend_keywords);
          window.localStorage.setItem('hotSearch', JSON.stringify({
            expires: Date.now() + 12 * 60 * 60 * 1000,
            result: response.data.data
          }));
          content.commit(types.HOT_SEARCH_GET, {
            result: response.data.data
          });
        })
      }
    },
    [types.SEARCH_KEY_WORD](content, keywords) {
      searchKeyWord(keywords).then(response => {
        // console.log(response.data.data.result.list);
        content.commit(types.SEARCH_KEY_WORD, {
          result: response.data.data.result.list
        })
      })
    }
  },
  modules: {}
});