<template>
  <div class="part">
    <img :src="item.image" @click="getToView">
    <div class="mask"></div>
    <div class="con">
      <p class="text">
        <span v-for="(cate, index) in item.cates" :key="index">{{cate.catename}}</span>
        <span>/</span>
        <span>{{cngTime(item.duration)}}</span>
      </p>
      <p class="title">{{item.title}}</p>
    </div>
    <transition name="custom-classes-transition" enter-active-class="animated fadeInUpBig
" leave-active-class="animated fadeOutDownBig">
      <video-detail v-if="videoDetailShow" @videoShow="videoDetailShow=$event"></video-detail>
    </transition>
  </div>
</template>

<script>
  import types from "@/store/TYPES.js";
  import VideoDetail from "@/components/VideoDetail.vue";
  export default {
    name: "ViewPart",
    components: {
      VideoDetail,
    },
    props: {
      item: Object
    },
    data() {
      return {
        videoDetailShow: false
      }
    },
    methods: {
      cngTime(v) {
        let m = parseInt((v / 60) % 60);
        let s = parseInt(v % 60);
        m = m < 10 ? "0" + m : m;
        s = s < 10 ? "0" + s : s;
        return m + "'" + s + '"';
      },
      getToView() {
        this.$store.dispatch(types.VIDEO_DETAIL_DATA, this.item.postid);
        this.videoDetailShow = true;
      },
    },
  }
</script>
<style lang="less" scoped>
  .part {
    position: relative;
    display: flex;

    img {
      vertical-align: middle;
      width: 100%;
      filter: brightness(.8);
    }

    .con {
      width: 100%;
      position: absolute;
      color: white;
      bottom: 0;
      left: 0;
      box-sizing: border-box;
      padding: 15px;

      .text {
        font-size: 12px;
        margin-bottom: 10px;

        span:nth-child(2) {
          margin: 0 5px;
        }
      }

      .title {
        font-size: 16px;
        font-weight: bold;
        width: 100%;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }


  }
</style>