<template>
  <div class="video">
    <van-nav-bar left-arrow @click-left="$emit('videoShow',false)" fixed z-index="7" />
    <video :src="VideoData.content_video[0].progressive[0].url" controls v-if="VideoData.content_video[0]" autoplay></video>
    <div class="con">
      <h3>{{VideoData.title}}</h3>
      <div class="sec">
        <span>{{showCate(VideoData.cate)}}</span><span>/</span><span>{{cngTime(VideoData.duration)}}</span></div>
      <p class="van-multi-ellipsis--l3">{{VideoData.intro}}</p>
      <span class="showArticle">阅读全文-></span>
    </div>
    <div class="author" v-if="VideoData.author">
      <div class="lt">
        <img :src="VideoData.author.profile.avatar" alt="">
      </div>
      <div class="center">
        <h3>{{VideoData.author.profile.username}}</h3>
        <span>片中职务：{{VideoData.author.roles}}</span>
      </div>
      <div class="rt">
        <van-icon name="arrow" />
      </div>
    </div>
    <SuggestVideo v-for="(item, index) in VideoData.relate_video" :key="index" :relate="item"></SuggestVideo>
    <VideoComment :comment="VideoData.comment"></VideoComment>
  </div>
</template>

<script>
  import {
    mapState
  } from "vuex";
  import SuggestVideo from "@/components/SuggestVideo.vue";
  import VideoComment from "@/components/VideoComment.vue";

  export default {
    name: "VideoDetail",
    components: {
      SuggestVideo,
      VideoComment
    },
    data() {
      return {
        clickShow: false
      }
    },
    methods: {
      cngTime(v) {
        let m = parseInt((v / 60) % 60);
        let s = parseInt(v % 60);
        m = m < 10 ? "0" + m : m;
        s = s < 10 ? "0" + s : s;
        return m + "'" + s + '"';
      },

    showCate(v) {
      return v.map(item => item).join('/')
    }
  },
  computed: {
    ...mapState({
      VideoData: state => state.detailData,
    })
  },
  }
</script>

<style lang="less">
  .video {
    position: fixed;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    background-color: white;
    color: black;
    left: 0;
    top: 0;
    z-index: 5;
    
    video{
      z-index:6
    }
    .van-nav-bar {
      background: rgba(0, 0, 0, 0);

      &.van-hairline--bottom {
        &::after {
          border: none;
        }
      }

      .van-nav-bar__left {
        .van-icon {
          color: white;
          font-size: 24px;
        }
      }
    }

    video {
      width: 100%;
      position: fixed;
    }

    .con,
    .author {
      padding: 10px 20px;
      margin-bottom: 30px;

      span {
        font-size: 12px;
        color: #999;
        margin: 0 5px;
      }
    }

    .con {
      margin-top: 240px;
      text-align: center;
      box-sizing: border-box;

      h3 {
        font-weight: bold;
        font-size: 18px;
        margin: 10px 0;
      }

      p {
        margin-top: 20px;
        text-align: left;
        margin-bottom: 20px;
      }

      .showArticle {
        color: skyblue;
        text-align: center;
        font-size: 14px;
      }
    }

    .author {
      border-top: 1px solid rgba(188, 188, 188, 0.3);
      border-bottom: 1px solid rgba(188, 188, 188, 0.3);
      display: flex;
      align-items: center;

      .lt {
        margin-right: 10px;
        height: 40px;
        width: 40px;

        img {
          width: 100%;
          border-radius: 50%;
        }
      }

      .center {
        flex: 1;

        h3 {
          font-weight: bold;
        }
      }
    }
  }
</style>