<template>
  <div class="home">
    <van-swipe v-if="banner" :autoplay="3000">
      <template>
        <van-swipe-item v-for="(item, index) in banner.list" :key="index"> 
          <img :src="item.image" />
        </van-swipe-item>
      </template>
    </van-swipe>
    <!-- 今天 -->
    <view-section v-if="today" :viewShow="today.list">今天</view-section>
    <!-- 热门 -->
    <view-section v-if="hot" :viewShow="hot.list" :isWrap="true">热门</view-section>

    <!-- 专题 -->
    <view-section v-if="album" :viewShow="album.list">专题</view-section>

    <!-- posts -->
    <view-section v-if="posts" :viewShow="posts.list">{{posts.selection_title}}</view-section>

    <!-- history 拉取 -->
    <van-list v-model="loading" :finished="finished" :offset="5" finished-text="没有更多了..." @load="onLoad">
      <section v-for="(item, index) in historyData" :key="index">
        <view-section :viewShow="item.data.list">{{item.data.selection_title}}</view-section>
      </section>
    </van-list>
  </div>
</template>

<script>
  import {
    mapState
  } from "vuex";
  import {
    mapActions
  } from "vuex";
  import types from "@/store/TYPES.js";
  import ViewSection from "@/components/ViewSection.vue";
  import {
    getIndexPosts
  } from "@/api/home.js";
  export default {
    name: "Home",
    components: {
      ViewSection
    },
    data() {
      return {
        loading: false,
        finished: false
      };
    },
    computed: {
      ...mapState({
        banner: state => state.indexData.banner,
        album: state => state.indexData.album,
        hot: state => state.indexData.hot,
        posts: state => state.indexData.posts,
        today: state => state.indexData.today,
        historyData: state => state.historyData
      })
    },
    methods: {
      ...mapActions([types.INIT_INDEX_DATA, types.GET_MORE_HISTORY_DATA]),
      // 触底拉取数据
      onLoad() {
        // url设置
        if (this.posts != undefined) {
          let url;
          if (this.historyData.length === 0) {
            url = this.posts.next_page_url;
          } else {
            url = this.historyData[this.historyData.length - 1].data.next_page_url;
          }
          // 数据加载过多 直接显示没数据
          if (this.historyData.length > 14) {
            this.finished = true;
          } else {
            getIndexPosts(url).then(response => {
              // 加载状态结束
              // console.log(response);
              setTimeout(() => {
                this.loading = false;
                this.$store.commit(types.GET_MORE_HISTORY_DATA, {
                  result: response.data
                });
              }, 2000);
            });
          }
        }
      }
    },
    created() {
      this.$store.dispatch(types.INIT_INDEX_DATA);
    }
  };
</script>
<style lang="less">
  .van-swipe {
    width: 100%;
    color: #fff;
    font-size: 20px;
    text-align: center;
    img {
      max-width: 100%;
      vertical-align: middle;
    }

    .van-swipe__indicator {
      width: 12px;
      height: 3px;
    }

    .van-swipe__indicator--active {
      background-color: white;
    }
  }
</style>