<template>
  <div id="app" @touchstart="touchstart" @touchend="touchend">
    <transition name="custom-classes-transition" enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut">
      <Calendar v-if="showCalendar" @calendar="showCalendar=$event" :calendarData="calendarData"></Calendar>
    </transition>
    <NavBar v-if="calendarData.time" @calendar="showCalendar=$event" :calendarData="calendarData.time.day"></NavBar>
    <!-- 路由输出 -->
    <router-view />
    <loading :showLoading="this.$store.state.loading" ></loading>
  </div>
</template>
<script>
  import NavBar from "@/components/NavBar.vue";
  import Calendar from "@/components/Calendar.vue";
  import Loading from "@/components/Loading.vue";
  import {
    mapState
  } from "vuex";
  import types from "@/store/TYPES.js";
  export default {
    components: {
      NavBar,
      Calendar,
      Loading
    },
    data() {
      return {
        showCalendar: false
      }
    },
    computed: {
      ...mapState({
        calendarData: state => state.calendarData
      })
    },
    created() {
      this.$store.dispatch(types.CALENDAR_DATA);
    },
    methods: {
      touchstart: function (event) {
        // console.log("开始", event.changedTouches[0].clientX);
        this.touchstartX = event.changedTouches[0].clientX;
        this.touchstartY = event.changedTouches[0].clientY;
      },
      touchend: function (event) {
        // console.log("结束", event.changedTouches[0].clientX);

        // 判断左滑动 返回上一页
        if (event.changedTouches[0].clientX - this.touchstartX > 150) {
          // console.log("从左到右");
          this.touchstartX = 0;
          this.touchstartY = 0;
          this.routerBack();
        }

        if (event.changedTouches[0].clientX - this.touchstartX < -150) {
          // console.log("从右到左");
          this.routerForward();
        }
      },
      routerBack: function () {
        this.$router.back();
      },
      routerForward: function () {
        this.$router.forward();
      }
    },
  };
</script>
<style lang="less">
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: black;
    color: white;
  }
</style>