<template>
  <div class="suggest">
    <div class="title"><span>{{relate.name}}</span> <span>more</span> </div>
    <van-swipe :loop="false" :width="200" :show-indicators="false">
      <van-swipe-item v-for="(item, index) in relate.list" :key="index" @click=getToView(item.postid)>
        <img :src="item.image" />
        <div class="msg">
          <span>{{cngTime(item.duration)}}</span>
          <h3 class="van-ellipsis">{{item.title}}</h3>
        </div>
      </van-swipe-item>
    </van-swipe>
    
  </div>
</template>

<script>
import types from "@/store/TYPES.js";
  export default {
    name: "SuggestVideo",
    props: ['relate'],
    methods: {
      cngTime(v) {
        let m = parseInt((v / 60) % 60);
        let s = parseInt(v % 60);
        m = m < 10 ? "0" + m : m;
        s = s < 10 ? "0" + s : s;
        return m + "'" + s + '"';
      },
      getToView(id) {
        this.$store.dispatch(types.VIDEO_DETAIL_DATA,id);
      }
    },
  }
</script>

<style lang="less">
  .suggest {
    padding: 0 15px;
    margin-bottom: 20px;
    .title {
      display: flex;
      padding: 10px 0;
      align-items: center;

      span:nth-of-type(1) {
        font-weight: bold;
        flex: 1;
      }
    }
    .van-swipe {
      background-color: white;
      .van-swipe-item {
        position: relative;
        margin-right: 5px;
        &::after{
          content: "";
          height: 100%;
          width: 100%;
          background-color: rgba(0, 0, 0, .2);
          z-index: 0;
          position: absolute;
          left: 0;
          top:0;
        }
        img {
          width: 100%;
          height: 120px;
        }
        .msg {
          position: absolute;
          left: 10px;
          bottom: 5px;
          text-align: left;
          width: 80%;
          font-size: 14px;
          span{
            margin-bottom: 5px;
            font-size: 12px;
          }
          z-index: 1;
        }
      }
    }
  }
</style>