// import vue from "Vue";
import "../plugins/axios";
export const getIndexData = function () {
    return window.axios.get('/apiv3/index/index').catch(error => {
        console.log(error);
    });
};
export const getIndexPosts=function (url) { 
    return window.axios.get(url).catch(error => {
        console.log(error);
    });
 }
export const getCalendar = function () {
    return window.axios.get('/apiv3/DayCover/getDayCover').catch(error => {
        console.log(error);
    });
};

export const detailData = function (id) {
    return window.axios.get('/apiv3/post/view',{
        params:{
            postid:id
        }
    }).catch(error => {
        console.log(error);
    });
};