<template>
  <div id="nav">
    <van-nav-bar class="navBar" fixed :placeholder="true" @click-left="$emit('calendar',true)" z-index="2"
      @click-right="searchBox=true">
      <template #left>
        <van-icon name="calender-o">
          <span>{{calendarData}}</span>
        </van-icon>
      </template>
      <template #title>
        <van-tabs v-model="activeName" @click="cngTab">
          <van-tab title="发现" name="/"></van-tab>
          <van-tab title="频道" name="/channel"></van-tab>
          <van-tab title="我的" name="/person"></van-tab>
        </van-tabs>
      </template>
      <template #right>
        <van-icon name="search" data-name="search" />
      </template>
    </van-nav-bar>
    <Search v-if="searchBox" @searchShow="searchBox=$event"></Search>
  </div>
</template>
<script>
  import Search from "@/components/Search.vue";
  export default {
    name: "NavBar",
    props: ['calendarData'],
    components: {
      Search
    },
    data() {
      return {
        activeName: '/',
        searchBox: false
      }
    },
    methods: {
      cngTab(name) {
        switch (name) {
          case "/":
            this.$router.replace('/').catch(err => {
              err
            });
            this.activeName = "/"
            break;
          case "/channel":
            this.$router.replace('channel').catch(err => {
              err
            });
            this.activeName = "/channel"
            break;
          case "/person":
            this.$router.replace('person').catch(err => {
              err
            });
            this.activeName = "/person"
            break;
        }
      }
    },
    watch: {
      $route(to) {
        this.cngTab(to.path)
        // console.log(from.path); //从哪来
        // console.log(to.path); //到哪去
      }
    },
    created() {
      this.cngTab(this.activeName);
    },
  }
</script>
<style lang="less">
  #nav {
    .navBar {
      background-color: black;
      color: white;

      .van-nav-bar {
        background-color: black;
      }
    }

    .van-tabs {
      .van-tabs__nav {
        background: none;
      }

      .van-tab {
        color: rgb(163, 163, 163);

        &.van-tab--active {
          color: white;
        }

        span {
          padding: 0 10px;
        }
      }

      .van-tabs__line {
        background-color: white;
      }
    }

    .van-icon {
      font-size: 24px;
      color: white;
    }

    .van-icon-calender-o {
      position: relative;

      span {
        font-size: 10px;
        position: absolute;
        top: 9px;
        left: 3px;
        width: 70%;
        height: 50%;
        transform: scale(0.8);
        background-color: black;

      }
    }

    a {
      font-weight: bold;
      color: #757575;
      padding: 0 5px;
      margin: 10px;
      line-height: 46px;

      &.router-link-exact-active {
        color: white;
      }
    }
  }
</style>