<template>
  <div class="detail">
    <van-nav-bar fixed left-arrow @click-left="$emit('detailShow',false);" :title="channelDetailData.title"
      v-if="channelDetailData">
    </van-nav-bar>
    <view-part v-for="(item, index) in detailData" :key="index" :item="item"></view-part>
    <van-list v-model="showNext" :finished="finished" :offset="5" finished-text="没有更多了" @load="onLoad()">
      <section v-for="(lists, index) in showDetailList" :key="index">
        <view-part v-for="(item, index) in lists" :key="index" :item="item"></view-part>
      </section>
    </van-list>
  </div>
</template>

<script>
  import ViewPart from "@/components/ViewPart.vue";
  import {
    getTabData
  } from "@/api/channel.js";
  import {
    getCateData
  } from "@/api/channel.js";
  export default {
    name: "ChannelDetail",
    components: {
      ViewPart
    },
    data() {
      return {
        finished: false,
        showNext: false,
        n: 1,
        showDetailList: []
      };
    },
    methods: {
      onLoad() {
        // console.log('xxx',this.channelDetailData.index,this);s
        setTimeout(() => {
          this.n = this.n + 1;
          let _this = this;
          if (
            this.$store.state.channelData[this.channelDetailData.num].cate_type !=
            "0"
          ) {
            getTabData(_this.n, _this.channelDetailData.tab).then(response => {
              // console.log(response.data.data,_this.n);
              _this.showDetailList.push(response.data.data);
            });
          } else {
            getCateData(_this.n, _this.channelDetailData.id).then(response => {
              // console.log(response.data.data);
              _this.showDetailList.push(response.data.data);
            });
          }
          this.showNext = false;
        }, 3000);
        if (this.showDetailList.length > 5) {
          this.finished = true;
        }
      }
    },
    props: ["detailData", "channelDetailData"]
  };
</script>

<style lang="less">
  .detail {
    position: fixed;
    z-index: 5;
    width: 100%;
    background: black;
    height: 100vh;
    left: 0;
    top: 0;
    overflow-y: scroll;

    .van-nav-bar {
      background-color: black;

      .van-icon {
        color: white;
      }

      .van-nav-bar__title {
        color: white;
      }
    }
  }
</style>