<template>
  <div class="show" @click="load()">
    <div class="pic">
      <img :src="theme.icon">
      <h3>#<slot></slot>#</h3>
    </div>
  </div>
</template>

<script>
import types from "@/store/TYPES.js";
  export default {
    name: 'ChannelShow',
    props:['theme','num'],
    methods:{
      load(){
        this.$store.dispatch(types.CHANNEL_DETAIL_DATA,{
          num:this.num,
          p:1,
          tab:this.theme.tab,
          id:this.theme.cateid!=undefined?this.theme.cateid:0,
        });
        let _this = this;
        this.$emit('channelDetailData',{
          num:_this.num,
          title:_this.theme.catename,
          tab:_this.theme.tab,
          id:_this.theme.cateid!=undefined?_this.theme.cateid:0,
        });
        this.$emit('detailShow',true);
      }
    },
 
  }
</script>

<style lang="less" scoped>
  .show {
    width: 50%;

    .pic {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .3);
        z-index: 0;
      }

      &:active {
        &::after {
          z-index: -1;
        }

        h3 {
          display: none;
        }
      }

      h3 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
      }
    }
  }
</style>