// import vue from "Vue";
import "../plugins/axios";
// 页面数据
export const getChannelData = function () {
    return window.axios.get('/apiv3/cate/getList').catch(error => {
        console.log(error);
    });
};
// 详情数据 通过tab拿到的
export const getTabData=function (p,tab) { 
    return window.axios.get('/apiv3/post/getPostByTab',{
        params:{
            p:p,
            size:10,
            tab:tab
        }
    }).catch(error => {
        console.log(error);
    });
 }
// 详情数据 通过cateid拿到的
export const getCateData=function (p,id) { 
    return window.axios.get('/apiv3/post/getPostInCate',{
        params:{
            p:p,
            size:10,
            cateid:id
        }}).catch(error => {
        console.log(error);
    });
 }